.grid-xs-fit {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(8px, 1fr));
  grid-template-rows: auto; }

.grid-xs-fill {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(8px, 1fr));
  grid-template-rows: auto; }

.grid-sm-fit {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(60px, 1fr));
  grid-template-rows: auto; }

.grid-sm-fill {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(60px, 1fr));
  grid-template-rows: auto; }

.grid-md-fit {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(144px, 1fr));
  grid-template-rows: auto; }

.grid-md-fill {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(144px, 1fr));
  grid-template-rows: auto; }

.grid-lg-fit {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(366px, 1fr));
  grid-template-rows: auto; }

.grid-lg-fill {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(366px, 1fr));
  grid-template-rows: auto; }

.grid-xl-fit {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(536px, 1fr));
  grid-template-rows: auto; }

.grid-xl-fill {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(536px, 1fr));
  grid-template-rows: auto; }

.grid-xxl-fit {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(768px, 1fr));
  grid-template-rows: auto; }

.grid-xxl-fill {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(768px, 1fr));
  grid-template-rows: auto; }

.grid {
  grid-gap: 12px; }
  .grid-xs {
    grid-gap: 2px; }
  .grid-sm {
    grid-gap: 4px; }
  .grid-md {
    grid-gap: 8px; }
  .grid-lg {
    grid-gap: 16px; }
  .grid-xl {
    grid-gap: 30px; }
  .grid-xxl {
    grid-gap: 1fr; }
